  border-color: var(--be-accent-default);
  font-weight: 500;
}
.admin-pill-nav .name {
  overflow: hidden;
  text-overflow: ellipsis;
}
.admin-pill-nav .actions {
  color: var(--be-secondary-text);
  margin-left: auto;
}
.admin-pill-nav .mat-icon-button {
  width: 30px;
  height: 30px;
  line-height: 30px;
}

button.admin-pill-nav-mobile-selector {
  display: flex;
  align-items: center;
  width: 100%;
  border-radius: 4px;
  border: 1px solid var(--be-divider-default);
  padding: 15px;
  text-transform: capitalize;
  background-color: var(--be-background);
}
button.admin-pill-nav-mobile-selector .mat-icon {
  margin-left: auto;
}

.admin-pill-nav-menu.mat-menu-panel {
  min-width: 200px;
}
.admin-pill-nav-menu.mat-menu-panel .mat-menu-item {
  text-transform: capitalize;
}
.admin-pill-nav-menu.mat-menu-panel .selected {
  color: var(--be-accent-default);
}

/* Theme for the ripple elements.*/
/* stylelint-disable material/no-prefixes */
/* stylelint-enable */
.be-dark-mode ::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}
.be-dark-mode ::-webkit-scrollbar-thumb {
  background-color: var(--be-divider-default);
  border-radius: 3px;
}
.be-dark-mode ::-webkit-scrollbar-thumb:hover {
  background-color: var(--be-divider-default);
}
.be-dark-mode .mat-slide-toggle-thumb {
  background-color: #bdbdbd;
}
.be-dark-mode select, .be-dark-mode option {
  background-color: var(--be-background);
  color: var(--be-text);
}
.be-dark-mode .mat-snack-bar-container {
  background-color: #fafafa;
  color: rgba(0, 0, 0, 0.87);
}
.be-dark-mode .mat-simple-snackbar-action {
  color: inherit;
}
.be-dark-mode .mat-pseudo-checkbox-disabled {
  color: #686868;
}
.be-dark-mode .mat-pseudo-checkbox-checked.mat-pseudo-checkbox-disabled, .be-dark-mode .mat-pseudo-checkbox-indeterminate.mat-pseudo-checkbox-disabled {
  background-color: #686868;
}
.be-dark-mode .mat-checkbox-disabled.mat-checkbox-checked .mat-checkbox-background, .be-dark-mode .mat-checkbox-disabled.mat-checkbox-indeterminate .mat-checkbox-background {
  background-color: #686868;
}
.be-dark-mode .mat-checkbox-disabled:not(.mat-checkbox-checked) .mat-checkbox-frame {
  border-color: #686868;
}
.be-dark-mode .mat-button-toggle-disabled.mat-button-toggle-checked {
  background-color: #424242;
}
.be-dark-mode material-navbar:not(.transparent).primary, .be-dark-mode material-navbar:not(.transparent).accent {
  background-color: var(--be-background);
  border-bottom: 1px solid var(--be-divider-lighter);
}
.be-dark-mode material-navbar:not(.transparent).primary .route-nav-item.active, .be-dark-mode material-navbar:not(.transparent).accent .route-nav-item.active {
  background-color: var(--be-accent-default);
}

.cdk-drag-placeholder {
  opacity: 0;
}

.cdk-drag-animating {
  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.cdk-drop-list-dragging .cdk-drag:not(.cdk-drag-placeholder) {
  transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

/* Theme for the ripple elements.*/
/* stylelint-disable material/no-prefixes */
/* stylelint-enable */
.chart-wrapper {
  flex: 1 1 auto;
  margin-bottom: 25px;
  border: 1px solid var(--be-divider-default);
  border-radius: 4px;
  background-color: var(--be-background);
}
.chart-wrapper:nth-child(1) {
  margin-right: 25px;
}

.pie-wrapper {
  max-width: 500px;
}

.chart-row {
  display: flex;
}

@media only screen and (max-width: 1024px) {
  .chart-row {
    display: block;
    margin-bottom: 0;
  }

  .chart-wrapper {
    margin-bottom: 10px;
  }
  .chart-wrapper:nth-child(1) {
    margin-right: 0;
  }
}
button.more-less-button {
  padding-left: 5px;
  font-weight: 500;
}

.play-button-animation.mat-mini-fab, .play-button-animation.mat-fab {
  transition: background 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1), transform 0.3s cubic-bezier(0, 0, 0, 2.41);
}
.play-button-animation.mat-mini-fab:hover, .play-button-animation.mat-fab:hover {
  transform: scale(1.2);
}